import Scroll from '../../base/scroll/scroll.vue'
import Step from '../../base/step/step.vue'
import Upload from '../../base/upload/upload.vue'

export default {
  name: 'getPrice1',
  components: {
    Step,
    Upload,
    Scroll
  },
  data () {
    return {
      stepArr: ['', ''],
      stepIndex: 0,
      isOk: true, // 
      twoInfo: {},
      goodsImg: [],
      text: '',
      keyWord: '',
      rbObj: {}
    }
  },
  watch: {
    goodsImg: {
      handler (val) {
        if (val.length > 3) {
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        }
      },
      deep: true
    }
  },
  // 
  created () {
    let that = this
    // 
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      buyerTaskRecordId: that.$route.query.buyerTaskRecordId
    }).then((res) => {
      let data = res.data
      if (data.code === '200') {
        this.twoInfo = data.data
      }
    }).catch(() => {
      this.$vux.alert.show({
        title: '',
        content: ''
      })
    })
    if (this.$route.query.rb) {
      // 
      this.$axios.post('/api/orderOperate/getTaskRecordByOrderId', {
        'orderId': this.$route.query.buyerTaskRecordId
      }).then((data) => {
        if (data.data.code === '200') {
          this.rbObj = data.data.data
          this.text = this.rbObj.additionalFavorText
          this.goodsImg = JSON.parse(this.rbObj.addfavorCheckId)
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        } else {
          this.$vux.alert.show({
            title: '',
            content: data.data.message
          })
        }
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  // 
  methods: {
    next () {
      console.log(this.text)
      if (this.text !== '' && this.goodsImg.length > 2) {
        this.$router.push({ name: 'preAppendTextImgFav2', query: { text: this.text, goodsImg: this.goodsImg, buyerTaskRecordId: this.$route.query.buyerTaskRecordId, rbObj: this.rbObj } })
      } else {
        this.$vux.alert.show({
          title: '',
          content: ''
        })
      }
    }
  }
}



